@import url(https://fonts.googleapis.com/css?family=Londrina+Shadow);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}html{font-family:helvetica, san-serif;line-height:1.3em;text-rendering:optimizeLegibility;-webkit-font-feature-settings:"kern";font-feature-settings:"kern";-webkit-font-kerning:normal;font-kerning:normal;-webkit-text-size-adjust:100%;-moz-text-size-adjust:100%;-ms-text-size-adjust:100%;text-size-adjust:100%}body{text-align:center;margin:0px}h1{line-height:1.2}a{color:black;text-decoration:none}ul,li{list-style:none;margin:0;padding:0}header{width:80%;margin:2rem;text-transform:uppercase}header h1{margin:0 0 0.5em;font-size:1rem}@media (min-width: 500px){header{display:flex;justify-content:space-between;align-items:flex-end}header h1{margin:0 1em 0 0}}@media (max-width: 500px){header h1{display:none}}@media (max-width: 500px){header{text-align:left}}header nav ul{display:flex;margin-bottom:4px;justify-content:space-between}@media (max-width: 767px){header nav ul{display:inline-flex;font-size:14px}}@media (min-width: 300px){header nav li{flex:auto;margin-right:1em;padding-right:1em;border-right:1px solid #000}}@media (min-width: 580px){header nav ul{flex-wrap:wrap}}header nav li:last-child{border:none;margin-right:0;padding-right:0}.hero{width:80%;margin:auto;margin-bottom:6vw}.hero h1{-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;-webkit-writing-mode:sideways-lr;-ms-writing-mode:sideways-lr;writing-mode:sideways-lr;font-size:6vw;font-weight:200;float:left;margin:0}.hero img{display:block;width:85%;max-height:90vh}hr{margin:0}

form{width:50%;margin:0 auto;text-align:left;padding:4vw 0vw}@media (max-width: 500px){form{width:80%}}form h1{font-weight:200;font-size:5vw;margin:0rem;padding-bottom:2rem;text-align:center}@media (max-width: 500px){form h1{padding-bottom:1rem}}form p{font-size:12px;font-weight:200}@media (max-width: 500px){form p{margin:.5rem;font-size:3vw}}form input{width:100%;padding:1rem;border:1px solid #dbd5d5}@media (max-width: 500px){form input{padding:.5rem}}form textarea{width:100%;padding:1rem;height:10vw;border:1px solid #dbd5d5}@media (max-width: 500px){form textarea{padding:.5rem}}form img{display:block;max-width:100%;height:auto;padding:2rem 0rem}form input[type=submit],form button[type=submit]{background-color:#DDD;color:black;font-size:12px;font-weight:500;width:100%;padding:.8rem;margin-top:20px}

.submissions-container{width:60%;margin:0 auto;display:grid;grid-gap:2rem;font-family:Avenir, Roboto, Helvetica, san-serif;font-size:90%;text-align:left}@media (max-width: 767px){.submissions-container{width:90%}}.submissions-container .submission{display:flex;border:5px solid #DDD;flex-flow:column;padding:2rem}.submissions-container h1{margin:0rem;line-height:1.2;font-weight:normal}.submissions-container p{margin:0.6em 0 2rem}.submissions-container img{display:block;max-width:100%;height:auto}

